import React from "react";

const Navbar: React.FC = () => {
  return (
    <nav className="bg-[#FFFFFF] w-[1248px] mx-auto my-[38px] rounded-[48px] shadow-lg shadow-[0_-10px_15px_-3px_rgba(0,0,0,0.1),_0_-4px_6px_-4px_rgba(0,0,0,0.1)]">
      <div className="max-w-7xl mx-auto flex items-center justify-between py-4 px-8">
        {/*Logo*/}
        <h1 className="font-normal text-[32px] leading-[20px] text-gray-800 ">
          DriveEasy
        </h1>

        {/*Navigation Links*/}
        <ul className="flex space-x-8 text-gray-600">
          <li className="hover:text-blue-600 cursor-pointer">Home</li>
          <li className="hover:text-blue-600 cursor-pointer">Browse Cars</li>
          <li className="hover:text-blue-600 cursor-pointer">My Bookings</li>
          <li className="hover:text-blue-600 cursor-pointer">Contact Us</li>
          <li className="hover:text-blue-600 cursor-pointer">About Us</li>
        </ul>

        {/*Buttons*/}
        <div className="flex space-x-4">
          <button className="border border-#212121-400 text-#212121-400 px-4 py-2 rounded-full hover:border-blue-600 hover:text-blue-600 transition">
            Login
          </button>
          <button className="bg-[#212121] text-[#FFFFFF] px-4 py-2 rounded-full hover:bg-gray-700 transition">
            Sign Up
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
